<template>
  <div class="edit container">
    <h1 class="page-header">编辑用户
        <span class="pull-right">
          <router-link :to="'/detail/' + user.id" class="btn btn-default">取消</router-link>
        </span>
      </h1>
      <form @submit.prevent="updateUser">
        <div class="well">
          <h4>用户信息</h4>
          <div class="form-group">
            <label>姓名</label>
            <input type="text" class="form-control" placeholder="name" v-model="user.name"/>
          </div>
          <div class="form-group">
            <label>电话</label>
            <input type="text" class="form-control" placeholder="phone" v-model="user.phone"/>
          </div>
          <div class="form-group">
            <label>邮箱</label>
            <input type="text" class="form-control" placeholder="email" v-model="user.email"/>
          </div>
          <div class="form-group">
            <label>学历</label>
            <input type="text" class="form-control" placeholder="education" v-model="user.education"/>
          </div>
          <div class="form-group">
            <label>毕业学校</label>
            <input type="text" class="form-control" placeholder="graduationschool" v-model="user.graduationschool"/>
          </div>
          <div class="form-group">
            <label>职业</label>
            <input type="text" class="form-control" placeholder="profession" v-model="user.profession"/>
          </div>
          <div class="form-group">
            <label>个人简介</label>
            <textarea class="form-control" rows="10" v-model="user.profile"></textarea>
          </div>
          <button type="submit" class="btn btn-primary">确定</button>
        </div>
      </form>
  </div>
</template>

<script>
export default {
  data() {
    return {
      user: {}
    }
  },
  methods: {
    fetchUser() {
      this.$http.post('/getuser' ,this.$route.params.id)
          .then( res => {
            // console.log(res)
            this.user = res.body
          })
    },
    updateUser() {
      this.$http.post('/updateuser', this.user)
          .then( res => {
            this.$router.push({path: '/detail/'+ this.$route.params.id, query: { alert: '用户信息更新成功！' }});
          })
    }
  },
  created() {
    this.fetchUser()
  },
}
</script>