<template>
  <div class="detail container">
    <Alert v-if="alert" :message="alert"></Alert>
    <h1 class="page-header">
        {{ user.name }}
         <span class="pull-right">
           <router-link class="btn btn-primary" :to="'/edit/' + user.id">
            编辑
          </router-link> 
          <button class="btn btn-danger" @click="deleteuser()">
            删除</button>
          <router-link to="/" class="btn btn-default">返回</router-link>
        </span>
    </h1>
    <ul class="list-group">
        <li class="list-group-item"><span class="glyphicon glyphicon-earphone">
          {{ user.phone }}
        </span></li>
        <li class="list-group-item"><span class="glyphicon glyphicon-envelope">
          {{ user.email }}
        </span></li>
      </ul>

    <ul class="list-group">
        <li class="list-group-item"><span class="glyphicon glyphicon-bookmark">
          {{ user.education }}
        </span></li>
        <li class="list-group-item"><span class="glyphicon glyphicon-cd">
          {{ user.graduationschool }}
        </span></li>
        <li class="list-group-item"><span class="glyphicon glyphicon-briefcase">
          {{ user.profession }}
        </span></li>
        <li class="list-group-item"><span class="glyphicon glyphicon-user">
          {{ user.profile }}
        </span></li>
      </ul>
  </div>
</template>

<script>
import Alert from '../components/Alert.vue'
export default {
  data() {
    return {
      alert: '',
      user: {}
    }
  },
  methods: {
    fetchUser() {
      this.$http.post('/getuser' ,this.$route.params.id)
          .then( res => {
            // console.log(res)
            this.user = res.body
          })
    },
    deleteuser() {
      this.$http.post('/deleteuser', this.$route.params.id)
          .then( res => {
            this.$router.push({path:'/', query: {alert: '用户信息删除成功'}})
          })
    }

  },
  created() {
    this.fetchUser()
    if ( this.$route.query.alert ) {
      this.alert = this.$route.query.alert
    }
  },
  components: {
    Alert
  }
}
</script>

<style scoped>
  button.btn-danger {
    margin: 0 10px;
  }
</style>
