<template>
  <div class="home container">
    <Alert v-if="alert" :message="alert"></Alert>
    <h1 class="page-header">用户管理系统
      <span class="pull-right">
        <router-link class="btn btn-primary" to="/add">
            添加用户
        </router-link> 
      </span>
    </h1>
    <input type="text" class="form-control" placeholder="搜索" v-model="filterInput">
    <table class="table table-striped table-bordered table-hover">
      <thead>
        <tr>
          <th>姓名</th>
          <th>电话</th>
          <th>邮箱</th>
          <th></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="customer in filterUserList" :key="customer.id">
          <td>{{ customer.name }}</td>
          <td>{{ customer.phone }}</td>
          <td>{{ customer.email }}</td>
          <td><router-link :to="'/detail/' + customer.id" class="btn btn-sm btn-default">详情</router-link></td>
        </tr>
      </tbody>
      <tfoot>
        
      </tfoot>
    </table>
  </div>
</template>

<script>
import Alert from '../components/Alert.vue'
export default {
  data() {
    return {
      alert:'',
      userlist: [],
      filterInput: ''
    }
  },
  computed: {
    filterUserList() {
      var value = this.filterInput
      value = '/' + value + '/i'
      return this.userlist.filter( function (item) {
        // 一定要使用 eval 函数先进行计算后再传入 match 中
        return item.name.match(eval(value));
      })
    }
  },
  methods: {
    fetchlist() {
      this.$http.get('/getlist')
          .then( res => {
            this.userlist = res.body
          })
    }
  },
  created() {
    this.fetchlist()
    // 判断路由是否带有查询参数
    if ( this.$route.query.alert ) {
      this.alert = this.$route.query.alert
    }
  },
  components: {
    Alert
  }
}
</script>

<style scoped>
  table {
    margin-top: 15px;
  }
  td,th {
    text-align: center;
  }
</style>
